
.center {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.startgame {
  /* padding-top: 50%; */
}

.app {
  /* width: 396px; */
  margin: 0 auto;
}

.section {
  height: 33%;
  width: 100%;
  top: 0;
  left: 0;
}

.choices {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 5%;
}

.correctchoice {

}

.incorrectchoice {

}

.choice {
  width: 40px;
}

.question {
  font-family: 'Cutive Mono', monospace;
  text-align: center;
  margin: 5%;
}

.answer {
  font-family: 'Cutive Mono', monospace;
  margin: 5% 10% 5% 13%;
  text-align: center;
  width: auto;
  /* justify-content: center; */
}

.next {
  font-family: 'Cutive Mono', monospace;
  text-align: right;
  margin-right: 9%;
}

.results {
  font-family: 'Cutive Mono', monospace;
  text-align: right;
  margin: 0 10% 10% 10%;
}

.playagain {
  font-family: 'Cutive Mono', monospace;
  text-align: right;
  margin-right: 9%;
}

.white {
  background: white;
  color: black;
}

.red {
  background: red;
  color: black;
}

.green {
  background: green;
  color: black;
}

.fadein {
  animation: slidein 3s ease-in 1s infinite reverse both running;
}
